import React from "react";
import { faStar } from '@fortawesome/free-solid-svg-icons';
import { faCirclePlus } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useDispatch } from 'react-redux';
import { storeCardData } from '../../../actions/cardActions';

function Card({ gambar, name, harga, rate }) {
  const dispatch = useDispatch();

  const handleClick = () => {
    const cardData = {
      gambar,
      name,
      harga,
      rate
    };
    dispatch(storeCardData(cardData));
    window.alert("Item added to cart!"); // Display the alert
  };

  return (
    <div className="halaman mx-2 my-4 sm:w-1/2 md:w-1/3 lg:w-1/4 xl:w-1/5">
      <div className="product text-center subpixel-antialiased bg-white p-4 rounded-lg drop-shadow-xl">
        <img src={gambar} alt="barang" className="img-fluid mb-3" />
        <div className="flex justify-between items-start">
          <h2 className="font-[500] my-1 text-sm">{name}</h2>
        </div>
        <h4 className="text-sm mb-2 flex justify-between items-start">Price $ {harga}</h4>
        <div className="flex justify-between items-start">
          <div className="flex items-start mt-2">
            <FontAwesomeIcon icon={faStar} style={{ color: "#ffef3d" }} />
            <p className="font-semibold text-base text-zinc-400 ml-1">{rate}</p>
          </div>
          <button className="hover:scale-110" onClick={handleClick}>
            <FontAwesomeIcon icon={faCirclePlus} size="2x" className="button-icon" />
          </button>
        </div>
      </div>
      <style>
        {`
          .button-icon {
            color: #2a511f;
          }
          .button-icon:hover {
            color: blue; 
          }
        `}
      </style>
    </div>
  );
}

export default Card;
